// Ensure the component host element fills the drag clone container
live-data-drag-clone {
  display: block;
  width: 100%;
  height: 100%;
}

.livedata-preview {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  color: #495057;
  font-size: clamp(12px, 2vw, 14px);
  font-weight: 500;
}
